import React, { useContext, useState } from 'react'
import { loadStripe } from '@stripe/stripe-js'
import { CardElement, Elements, useStripe, useElements } from '@stripe/react-stripe-js'
import { ThemeContext } from 'styled-components'

import {
	Header,
	HeaderContent,
	HeaderTitle,
	DonationSection,
	DonationContainer,
	DonationCopy,
	DonationFormWrapper,
	DonationForm,
	DonationStripeWrapper,
	GiftRegistrySection,
	GiftRegistryContainer,
	GiftRegistryTitle,
	GiftRegistryCopy,
	GiftRegistryLinkWrapper,
	GiftRegisteryLink,
	GiftRegisteryLogo,
} from './styledComponents'
import { PageWrapper } from 'components'
import { registry } from 'config/images'
import { settings } from 'config/settings'
import { TEButton, TEInputRow, TETextareaRow } from 'react-tec'
import { apiRequest } from 'helpers/apiHelper'

const stripePromise = loadStripe(settings.STRIPE_API_KEY)

const CheckoutForm = () => {
	const stripe = useStripe()
	const elements = useElements()
	const theme = useContext(ThemeContext)

	const [firstName, setFirstName] = useState('')
	const [lastName, setLastName] = useState('')
	const [note, setNote] = useState('')
	const [amount, setAmount] = useState('')

	const CARD_OPTIONS = {
		iconStyle: 'solid' as 'solid', //hacky as shit
		style: {
			base: {
				iconColor: theme.primary,
				color: theme.primary,
				fontWeight: '500',
				fontFamily:
					"'Josefin Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif",
				fontSize: '16px',
				fontSmoothing: 'antialiased',
				':-webkit-autofill': { color: theme.primary },
				'::placeholder': { color: theme.gray },
			},
			invalid: {
				iconColor: '#red',
				color: '#red',
			},
		},
	}
	const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
		e.preventDefault()
		//TODO: review typing. Feels weird
		try {
			const { error, paymentMethod } = await stripe!.createPaymentMethod({
				type: 'card',
				card: elements!.getElement(CardElement)!,
			})
			console.log(error, paymentMethod)
			if (error) {
				console.log(error)
				return
			}
			const data = {
				amount: Number(amount),
				source: paymentMethod,
				metadata: { firstName, lastName, note },
			}
			await apiRequest({ method: 'post', path: 'stripe_charge_card', data })
		} catch (e) {
			console.log(e)
		}
	}

	return (
		<DonationForm onSubmit={handleSubmit}>
			<TEInputRow
				labelForKey='firstName'
				title='First Name'
				value={firstName}
				onChange={(e) => setFirstName(e.target.value)}
				rowSize='half'
				required
			/>
			<TEInputRow
				labelForKey='lastName'
				title='Last Name'
				value={lastName}
				onChange={(e) => setLastName(e.target.value)}
				rowSize='half'
				last
				required
			/>
			<TETextareaRow
				labelForKey='note'
				title='Leave Us A Message'
				value={note}
				onChange={(e) => setNote(e.target.value)}
			/>
			<TEInputRow
				type='number'
				labelForKey='amount'
				title='Donation Amount'
				value={amount}
				onChange={(e) => setAmount(e.target.value)}
				min='0'
				step='0.01'
				required
			/>
			<DonationStripeWrapper>
				<CardElement options={CARD_OPTIONS} />
			</DonationStripeWrapper>
			<TEButton type='submit' disabled={!stripe}>
				Submit
			</TEButton>
		</DonationForm>
	)
}

export const Registry = () => {
	return (
		<PageWrapper>
			<Header>
				<HeaderContent>
					<HeaderTitle>We are so blessed</HeaderTitle>
				</HeaderContent>
			</Header>
			<DonationSection>
				<DonationContainer>
					<DonationCopy>
						We are so happy that you’ll be able to join us for our wedding. As many of
						you know, we live in a small and cozy apartment, and it is already filled
						with many things we need to make it a home. While the presence of your
						company is the only gift we could ever ask for, a Wishing Well and a gift
						registry has been set up here for those who have expressed an interest in
						celebrating the occasion. Contributions to our Wishing Well will go towards
						a honeymoon or our future home when we are ready to set roots!
					</DonationCopy>
					<DonationFormWrapper>
						<Elements stripe={stripePromise}>
							<CheckoutForm />
						</Elements>
					</DonationFormWrapper>
				</DonationContainer>
			</DonationSection>
			<GiftRegistrySection>
				<GiftRegistryContainer>
					<GiftRegistryTitle>Gift Registry</GiftRegistryTitle>
					<GiftRegistryCopy>
						Below are locations we have registered with.
					</GiftRegistryCopy>
					<GiftRegistryLinkWrapper>
						<GiftRegisteryLink href='https://www.crateandbarrel.com/gift-registry/courtney-tate-and-stuart-casarotto/r6106794'>
							<GiftRegisteryLogo
								src={registry.crateAndBarrel}
								alt='Crate and Barrel Wedding Registery'
							/>
						</GiftRegisteryLink>
						<GiftRegisteryLink href='https://www.amazon.com/wedding/registry/347Y1S7FUI6SD?ref=wr_search_page_result_1'>
							<GiftRegisteryLogo
								src={registry.amazon}
								alt='Amazon Wedding Registery'
							/>
						</GiftRegisteryLink>
					</GiftRegistryLinkWrapper>
				</GiftRegistryContainer>
			</GiftRegistrySection>
		</PageWrapper>
	)
}
